* {
    margin: 0;
    padding: 0;
}

a, a:hover {
    text-decoration: none;
}

button:focus {
    outline: 0;
}

ul {
    list-style: none
}

html,
body,
#root {
    width: 100%;
    height: 100%;
    background-color: #f7f7f7
}

/* //////////////////////////////header部分////////////////////////// */

/* header_title部分 */
.header_title {
    font-family: 'Times New';
    color: #333333;
    font-weight: 700;
    font-size: 1.5em;
    text-decoration: none;
}

.header_title a {
    color: #333333;
}

.header_title a:hover {
    color: #333333;
    text-decoration: none;
}

/* 个人简介部分 */
.description {
    font-family: 'Times New Roman, Times, serif';
    font-size: 15px;
    font-weight: 200;
    color: #888;
    margin: 0;
    padding: 0;
    display: block;
    -webkit-margin-before: 1em;
            margin-block-start: 1em;
    -webkit-margin-after: 1em;
            margin-block-end: 1em;
    -webkit-margin-start: 0px;
            margin-inline-start: 0px;
    -webkit-margin-end: 0px;
            margin-inline-end: 0px;
}

/* nav-links部分 */

.nav_links {
    font-family: 'Times New Roman';
    font-size: 16px;
    font-style: italic
}

.nav_link {
    display: inline-block;
}

.nav_link a {
    color: #333;
    text-decoration: none;
    margin-right: 10px;
    border-bottom: 1px solid #bbb;
}

.nav_links a:hover {
    color: #333;
    text-decoration: none;
    border-bottom: 1px solid black
} 

/* 头像部分 */
.avatar {
    width: 100px;
    height: 100px;
    border-radius: 50%;
    border: 3px solid #f2f2f2;
    transition: -webkit-transform 1s;
    transition: transform 1s;
    transition: transform 1s, -webkit-transform 1s;
    -webkit-animation: fade-in-down .6s;
            animation: fade-in-down .6s;
    -webkit-animation-delay: .2s;
            animation-delay: .2s; 
}

.avatar:hover{
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg)
}

/* social_media部分 */
svg:hover path{
    cursor: pointer;
    fill: #515151;
}

.social_media_links li{
    display: inline-block;
}

.social_media_links a {
    margin-right: 10px;
}

/* 头部的info部分 */
.header_info {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
    -webkit-align-items: center;
            align-items: center;
    margin: 0;
    padding: 0;
}

/* 头部的blank部分 */
.header_blank {
    box-sizing: inherit;
    display: block;
    padding: 80px 0;
}

/* 头部的整体 */
.header {
    width: 80vw;
    max-width: 1300px;
    margin: 0 auto;
    display: block;
    padding: 0;
}

/* //////////////////////////footer部分///////////////////// */
.footer {
    font-family: 'Times New Roman';
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    padding: 30px 0;
    margin: 0 !important;
    color: #333;
    bottom: 0;
}

.footer p {
    margin: 0 !important;
    font-size: 13px;
    color: #888;
    display: block;
    -webkit-margin-before: 1em;
            margin-block-start: 1em;
    -webkit-margin-after: 1em;
            margin-block-end: 1em;
    -webkit-margin-start: 0px;
            margin-inline-start: 0px;
    -webkit-margin-end: 0px;
            margin-inline-end: 0px;
}

.footer a {
    color: #000;
    text-decoration: none;
}

.footer a:hover {
    color: #000;
    text-decoration: none;
    border-bottom: 1px solid black
}

/* ///////////////////////////content部分///////////////////////// */
.content {
    padding: 50px;
    position: relative;
    width: 80vw;
    max-width: 940px;
    margin: 50px auto 0;
    background: #fff;
    box-shadow: 0 10px 20px 0 hsla(0, 0%, 92.5%, .86);
    display: block
}

.conten_lists {
    list-style: none;
    margin: 0;
    padding: 0;
    display: block;
    list-style-type: disc;
    -webkit-margin-before: 1em;
            margin-block-start: 1em;
    -webkit-margin-after: 1em;
            margin-block-end: 1em;
    -webkit-margin-start: 0px;
            margin-inline-start: 0px;
    -webkit-margin-end: 0px;
            margin-inline-end: 0px;
    -webkit-padding-start: 40px;
            padding-inline-start: 40px;
}

.content_post {
    margin-bottom: 2.3em;
}

.content_post_meta {
    font-family: 'Times New Roman';
    display: block;
    color: #bbb;
    font-size: 12px;
    font-style: italic;
    font-weight: lighter;
}

.content_post_link {
    font-family: 'STKaiti';
    font-weight: lighter;
    font-size: 19px;
    color: #000;
    line-height: 1.3
}

.content_post_link:hover {
    color: #000;
    text-decoration: none;
    border-bottom: 1px solid black
}


.content_post_link::before {
    content: " ";
    position: absolute;
    top: 10px;
    left: -20px;
    width: 4px;
    height: 4px;
    border-radius: 2px;
    background: #1f1f1f;
    transition: all .2s cubic-bezier(.4,0,1,1);
}

.content_post_link:hover::before {
    content: " ";
    position: absolute;
    top: 10px;
    left: -26px;
    width: 10px;
    height: 4px;
    border-radius: 2px;
    background: #1f1f1f;
    transition: all .2s cubic-bezier(.4,0,1,1);
}

h3 {
    position: relative;
    display: block;
    font-size: 1.17em;
    -webkit-margin-before: 1em;
            margin-block-start: 1em;
    -webkit-margin-after: 1em;
            margin-block-end: 1em;
    -webkit-margin-start: 0px;
            margin-inline-start: 0px;
    -webkit-margin-end: 0px;
            margin-inline-end: 0px;
}

.content_pagination {
    overflow: auto;
    border-top: 1px solid #f1f1f1;
    padding-top: 2.5em;
    display: block
}

.content_blog_link {
    font-size: 14px;
    font-family: 'STKaiti';
    color: #888;
    float: right;
    text-decoration: none;
}

.content_blog_link:hover {
    color: #000;
    text-decoration: none;
    border-bottom: 1px solid black
}
/* //////////////////////////////header部分////////////////////////// */

/* blog_header整体部分 */
.blog_header {
    width: 60%;
    margin: 12px auto;
    position: relative;
    line-height: 1;
    text-align: left;
    padding-top: 50px;
    display: block;
}

/* blog_header导航栏部分 */
.blog_header_nav_links {
    margin-left: 10px;
    font-family: 'Times New Roman';
    font-size: 18px;
    font-style: italic;
}

.blog_header_nav_link {
    display: inline-block;
}

.blog_header_nav_link a
{
    text-decoration: none;
    color: #888; 
    margin-right: 20px;
}

.blog_header_nav_link a:hover {
    color: black;
    text-decoration: none;
    /* border-bottom: 1px solid black */
}

/* blog_header的分割线部分 */
.blog_header_hr hr {
    width: 100%;
    height: 1px;
    margin: 10px 0 40px;
    border: 0;
    padding: 0;
    background-image: -webkit-linear-gradient(0deg,#d5d5d5,#d5d5d5,transparent);
    overflow: visible;
}

