/* //////////////////////////////Blog_pages的格式部分////////////////////////// */
pre {
    /* font-family: monospace; */
    font-family: 'Times New Roman';
    margin: 1.3em 0px!important;
    border: 1.3px solid #ccc;
    padding: 1em 1em;
}

blockquote {
    color: #666;
    margin: 0;
    padding-left: 3em;
    border-left: 0.5em #eee solid;
}
